@import url(partials/reset.css);

@import url(partials/fonts.css);

/* my style */

a {
  text-decoration: none;
}

html {
  font-family: sans-serif;
  line-height: 1.2;
}

body {
  margin: 0;
  font-size: 1rem;
  color: #000000;
  font-weight: 400;
  font-family: "PT Serif Pro";
  text-align: left;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  max-width: 1170px;
}

img {
  max-width: 100%;
  height: auto;
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 179px;
  height: 51px;
  border-radius: 63% 8% 44% 10%/86% 23% 88% 24%;
  background: #da1f8d;
  box-shadow: 0 26px 46px rgba(0, 0, 0, 0.24), 0 3px 0 #952165;
  font-family: Roboto;
  color: #ffffff;
  font-size: 12px;
  font-weight: 700;
}

.site-header {
  padding-top: 25px;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  max-width: 1156px;
  width: calc(100% - 30px);
}

.site-header--fixed {
  position: fixed;
  height: 71px;
  box-shadow: 0 18px 46px rgba(0, 0, 0, 0.2), 0 3px 0 rgba(66, 106, 173, 0.48);
  border-radius: 10px;
  background-color: #ffffff;
  left: 50%;
  transform: translateX(-50%);
  padding: 0 27px;
}

.site-header--fixed .sh-logo {
  width: 153px;
}

.site-header--fixed .shc-work-time {
  display: none;
}

.site-header--fixed .shc-work-phone {
  line-height: 131%;
}

.site-header--fixed .sh-contacts__right .btn {
  width: 146px;
  height: 42px;
}

.sh-burger {
  display: none;
}

.sh-logo {
  display: block;
  width: 191px;
}

.sh-menu {
  display: flex;
  align-items: center;
}

.sh-menu__link {
  margin: 0 7.5px;
  color: #000000;
  font-family: Montserrat;
  font-weight: 400;
  padding-bottom: 8px;
  font-size: 14px;
}

.sh-menu__link--active {
  border-bottom: 1px solid #000;
}

.sh-menu__link:hover {
  color: #da1f8d;
}

.sh-contacts {
  display: flex;
}

.sh-contacts__left {
  margin: 0 21px;
}

.shc-work-time {
  font-family: Montserrat;
  color: #da1f8d;
  font-size: 12px;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-right: 11px;
  margin-bottom: 4px;
  margin-top: 6px;
}

.shc-work-time svg {
  width: 11px;
  margin-right: 9px;
  height: 11px;
  fill: #da1f8d;
}

.shc-work-phone {
  display: flex;
  align-items: center;
  font-family: Montserrat;
  color: #000000;
  font-size: 26px;
  font-weight: 600;
}

.shc-work-phone img {
  width: 11px;
  height: 11px;
  margin-right: 9px;
}

@media screen and (max-width: 1200px) {
  .sh-burger {
    display: block;
    width: 30px;
    height: 18px;
    cursor: pointer;
    position: relative;
  }

  .sh-burger span,
  .sh-burger span::before,
  .sh-burger span::after {
    position: absolute;
    top: 50%;
    margin-top: -1px;
    left: 50%;
    margin-left: -15px;
    width: 30px;
    height: 2px;
    background-color: #000;
  }

  .sh-burger span::before,
  .sh-burger span::after {
    content: '';
    transition: 0.2s;
  }

  .sh-burger span::before {
    transform: translateY(-8px);
  }

  .sh-burger span::after {
    transform: translateY(8px);
  }

  .sh-burger--active span {
    height: 0;
  }

  .sh-burger--active span::before {
    transform: rotate(45deg);
  }

  .sh-burger--active span::after {
    transform: rotate(-45deg);
  }

  .sh-logo,
  .site-header--fixed .sh-logo {
    width: 250px;
  }

  .site-header {
    position: fixed;
    top: 0;
    height: 71px;
    box-shadow: 0 18px 46px rgba(0, 0, 0, 0.2), 0 3px 0 rgba(66, 106, 173, 0.48);
    border-radius: 10px;
    background-color: #ffffff;
    left: 50%;
    transform: translateX(-50%);
    padding: 0 27px;
  }

  .site-header .sh-logo {
    width: 153px;
  }

  .site-header .shc-work-time {
    display: none;
  }

  .site-header .shc-work-phone {
    line-height: 131%;
  }

  .site-header .sh-contacts__right .btn {
    width: 146px;
    height: 42px;
  }

  .site-header + * {
    margin-top: 71px;
  }

  .sh-menu {
    display: none;
    position: absolute;
    box-shadow: 0 18px 46px rgba(0, 0, 0, 0.2), 0 3px 0 rgba(66, 106, 173, 0.48);
    border-radius: 10px;
    flex-direction: column;
    align-items: center;
    top: 74px;
    background: #fff;
    width: 100%;
    left: 0;
  }

  .sh-menu__link {
    display: block;
    text-align: center;
    margin: 10px auto;
    color: #000000;
    font-family: Montserrat;
    font-weight: 400;
    padding-bottom: 8px;
    font-size: 14px;
  }

  .sh-menu__link--active {
    border-bottom: 1px solid #000;
  }

  .sh-menu__link:hover {
    color: #da1f8d;
  }

  .sh-contacts {
    display: none;
  }
}

@media only screen and (max-width: 1199px) {
  .container {
    max-width: 960px;
  }
}

@media only screen and (max-width: 991px) {
  .container {
    max-width: 720px;
  }
}

@media only screen and (max-width: 767px) {
  .container {
    max-width: 320px;
  }
}

@media screen and (max-width: 500px) {
  .sh-logo,
  .site-header--fixed .sh-logo {
    width: 173px;
  }

  .site-header,
  .site-header--fixed {
    width: 100%;
  }
}